.js-scroll-item.is-shown {
  animation: fadein 4s;
}

@keyframes header_off1 {
  0% {
    left: 0%;
    opacity: 0;
  }
  15%,
  85% {
    left: 0;
    opacity: 1;
  }
  100% {
    left: 100%;
    opacity: 0;
  }
}

.line-run {
  animation: header_off1 5s ease-in-out 2s infinite;
  width: 5px;
  height: 40px;
  background: #fff;
  position: relative;
  display: block;
  transform: rotate(25deg);
  margin-top: -47px;
  margin-left: -16px;
}

@keyframes fadeup {
  0% {
    transform: translateY(30px);
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.promise__content-item {
  opacity: 0;
}

.promise__content-item.js-scroll-item.is-shown {
  animation: fadeup 1s cubic-bezier(0.33, 1, 0.68, 1) 1 forwards;
}

.promise__content-item.js-scroll-item.is-shown .line {
  animation: fadeup 4s cubic-bezier(0.76, 0.04, 0, 0.95) 1 forwards;
}

.product__item, .belleza__item, .media__item-box ul {
  opacity: 0;
}

.product__item.js-scroll-item.is-shown, .belleza__item.js-scroll-item.is-shown, .media__item-box ul.js-scroll-item.is-shown {
  animation: fadeup 1s cubic-bezier(0.33, 1, 0.68, 1) 1 forwards;
}

.pict {
  position: relative;
}
